@import '../../scss/colors';

.pokemon__details-wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position:relative;
}

.details__card {
  background: rgb(57, 57, 119);
  width: 85%;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  border-radius: 8px;
  padding: 10px;
  box-sizing: border-box;
  animation-duration: 3s;
  animation-name: intro;
  animation-iteration-count: 1;
  animation-timing-function: linear;
}
@keyframes intro {
  from {
    transform: scale(3) rotate(-150deg);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}


.details__image {
  position: absolute;
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  height: 120px;
}

.details__name {
  color: $color-yellow;
  margin: 60px auto 30px;
  text-transform: capitalize;
  padding-bottom: 10px;
  border-radius: 10px;
  max-width: 200px;
}

.pokemon__data-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid grey;
  padding-bottom: 10px;
  margin: 0 auto 20px;
  max-width: 255px;
  .data__container {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
    padding: 0 5px;
  }
}

.data__content {
  color: $color-yellow;
  font-weight: bold;
}

.data__type {
  color: $color-yellow;
  text-transform: uppercase;
  font-size: 12px;
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.data__content-types {
  display: flex;
  justify-content: center;
  align-items: center;
}
.details__abilities {
  list-style: none;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.label {
  color: $color-yellow;
}

.button__back {
  position: absolute;
  top: 50px;
  left: 40px;
  font-weight: bold;
  text-transform: capitalize;
  display: flex;
  justify-content: center;
  align-items: center;
  @media all and (min-width: 550px) {
    top: 50px;
    left: 100px;
  }
  color: $color-black;
  text-decoration: none;
  .back__icon {
    height: 50px;
  }
}
